import Vue from 'vue'
import VueRouter from 'vue-router'

// 懒加载路由组件
const Login = () => import('../views/Login.vue')
const Home = () => import('../views/Home.vue')
const EnterpriseInit = () => import('../views/EnterpriseInit.vue')
const AccountSubject = () => import('../views/AccountingSubject.vue')
const Supplier = () => import('../views/Supplier.vue')
const Customer = () => import('../views/Customer.vue')
const Employee = () => import('../views/Employee.vue')
const PurchaseOrder = () => import('../views/PurchaseOrder.vue')
const SalesOrder = () => import('../views/SalesOrder.vue')
const EmployeeExpense = () => import('../views/EmployeeExpense.vue')
// 新增功能组件
const JournalEntry = () => import('../views/JournalEntry.vue')
const Posting = () => import('../views/Posting.vue')
const Reconciliation = () => import('../views/Reconciliation.vue')
const TaxHandling = () => import('../views/TaxHandling.vue')
const RegularBusiness = () => import('../views/RegularBusiness.vue')
const FinancialReport = () => import('../views/FinancialReport.vue')

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  {
    path: '/home',
    component: Home,
    children: [
      { path: 'enterprise-init', component: EnterpriseInit },
      { path: 'accounting-subject', component: AccountSubject },
      { path: 'supplier', component: Supplier },
      { path: 'customer', component: Customer },
      { path: 'employee', component: Employee },
      { path: 'purchase-order', component: PurchaseOrder },
      { path: 'sales-order', component: SalesOrder },
      { path: 'employee-expense', component: EmployeeExpense },
      { path: 'journal-entry', component: JournalEntry },
      { path: 'posting', component: Posting },
      { path: 'reconciliation', component: Reconciliation },
      { path: 'tax-handling', component: TaxHandling },
      { path: 'regular-business', component: RegularBusiness },
      { path: 'financial-reports', component: FinancialReport }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 路由守卫
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token')
  if (to.path === '/login') {
    next()
  } else {
    if (token) {
      next()
    } else {
      next('/login')
    }
  }
})

export default router
